
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.v-enter-active {
  -webkit-transition: opacity 1.5s;
  transition: opacity 1.5s;
}
.v-enter {
  opacity: 0;
}
